<template>
  <div>
    <p> Tng lt truy cp: {{ totalVisitors }}</p>
    <p> Hm nay: {{ todayVisitors }}</p>
  </div>
</template>

<script>
import axios from "axios";
const API_URL = "https://baos-album-backenddd.onrender.com";

export default {
  data() {
    return {
      totalVisitors: 0,
      todayVisitors: 0,
    };
  },
  async mounted() {
    try {
      // Kim tra nu  ghi nhn lt truy cp hm nay cha
      const lastVisit = localStorage.getItem("lastVisit");
      const today = new Date().toDateString();

      if (lastVisit !== today) {
        await axios.post(`${API_URL}/api/visit`);
        localStorage.setItem("lastVisit", today); // Lu ngy  truy cp
      }

      // Ly s lt truy cp
      const response = await axios.get(`${API_URL}/api/visitor-count`);
      this.totalVisitors = response.data.total;
      this.todayVisitors = response.data.today;
    } catch (error) {
      console.error("Li khi ly s lng truy cp:", error);
    }
  },
};
</script>
